import Vue from 'vue'
import VueRouter from 'vue-router'

// Plugins
import GlobalComponents from './globalComponents'
import GlobalDirectives from './globalDirectives'
import Notifications from './components/UIComponents/NotificationPlugin'
import SideBar from './components/UIComponents/SidebarPlugin'
import SideBarForManagers from './components/UIComponents/SidebarPluginForManagers'
import SideBarForStudent from './components/UIComponents/SidebarPluginForStudent'
import SideBarForAdmin from './components/UIComponents/SideBarForAdmin'
import Vuetable from 'vuetable-2/src/components/Vuetable'
import VueTablePagination from 'vuetable-2/src/components/VueTablePagination'
import VueTablePaginationDropDown from 'vuetable-2/src/components/VueTablePaginationDropDown'
import VueTablePaginationInfo from 'vuetable-2/src/components/VueTablePaginationInfo'

import App from './App'

// router setup
import routes from './routes/routes'

// library imports
import Chartist from 'chartist'
import 'bootstrap/dist/js/bootstrap.min.js'
import 'bootstrap/dist/css/bootstrap.css'
import './assets/sass/paper-dashboard.scss'
import 'es6-promise/auto'

// plugin setup
Vue.use(VueRouter)
Vue.use(GlobalComponents)
Vue.use(GlobalDirectives)
Vue.use(Notifications)
Vue.use(SideBar)
Vue.use(SideBarForStudent)
Vue.use(SideBarForAdmin)
Vue.use(SideBarForManagers)
Vue.use(Vuetable)
Vue.use(VueTablePagination)
Vue.use(VueTablePaginationDropDown)
Vue.use(VueTablePaginationInfo)

// configure router
const router = new VueRouter({
  routes, // short for routes: routes
  linkActiveClass: 'active'
})

// global library setup
Object.defineProperty(Vue.prototype, '$Chartist', {
  get () {
    return this.$root.Chartist
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  router,
  data: {
    Chartist: Chartist
  }
})



